<template>
    <div id="access-table">
        <div class="table-title"></div>

        <!-- btn start -->
        <div class="search-content clearfix">
            <div class="search-left left clearfix">
                <el-input v-model="searchName" placeholder="" class="left"></el-input>

                <el-button class="searchBtn" @click="getList"></el-button>
            </div>

            <div class="search-right right">
                <el-button @click="goAccessEditor"></el-button>
                <el-button @click="goDraft">{{draftNum}}</el-button>
                <el-button class="red" @click="muDialogDeleteOpen"></el-button>
            </div>
        </div>
        <!-- btn end -->

        <!-- table start -->
        <div class="table-content">
          <el-table class="table-list" :data="list" stripe height="680" @selection-change="handleSelectionChange"  @sort-change="sort">
              <el-table-column type="selection" align="center" width="38"></el-table-column>
              <el-table-column min-width="80" align="center" label="" sortable="true" prop="sort_food_id">
                  <template slot-scope="scope">
                      <span>{{ scope.row.food_id}}</span>
                  </template>
              </el-table-column>

              <el-table-column min-width="80" align="center" label="">
                  <template slot-scope="scope">
                      <img v-if="scope.row.food_picture === ''" src="./images/empty_small_pic_normal.png" alt="">
                      <img v-else :src="imgbase_url + '/img_get.php?img=1&height=69&width=69&imgname=' + scope.row.food_picture " alt="">
                  </template>
              </el-table-column>

              <el-table-column min-width="93" align="center" label="">
                  <template slot-scope="scope">
                      <span>{{ scope.row.food_name}}</span>
                  </template>
              </el-table-column>

              <el-table-column min-width="100" align="center" label="">
                  <template slot-scope="scope">
                      <span>&yen; {{ scope.row.food_price}}</span>
                  </template>
              </el-table-column>

              <el-table-column min-width="80" align="center" label="">
                  <template slot-scope="scope">
                      <span>{{ scope.row.category_name}}</span>
                  </template>
              </el-table-column>

              <el-table-column min-width="80" align="center" label="" sortable="true" prop="4">
                  <template slot-scope="scope">
                      <!-- <span v-if="scope.row.stock_num_day === 0">99+</span> -->
                      <span v-if="scope.row.stock_num_day === 99999">99+</span>
                      <span v-else>{{ scope.row.stock_num_day}}</span>
                  </template>
              </el-table-column>

              <el-table-column min-width="140" align="center" label="">
                  <template slot-scope="scope">
                      <span class="blue-text">
                          <router-link :to="{path:'/good/accesseditor',query: { foodId:scope.row.food_id}}">
                              
                          </router-link>
                      </span>
                      <span @click="dialogDeleteOpen(scope.row.food_id)" class="red-text" style="padding-left:10px;padding-top:10px;"></span>
                  </template>
              </el-table-column>
          </el-table>

          <div class="data-empty" v-if="isListEmpty">
            <div class="img">
              <img src="../../../assets/images/empty.png">
            </div>
            <div class="text">
              <span>...</span>
            </div>          
          </div>
        </div>
        <!-- table end -->

        <!--  starat -->
        <div class="pagination-container">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.page" :page-sizes="[10,20,40]" :page-size="listQuery.limit" layout="sizes, jumper, prev, pager, next" :total="total">
            </el-pagination>
        </div>
        <!--  end -->

        <!--  -->
        <el-dialog class="delete-dialog"
        :visible.sync="deleteDialogVisible"
        :show-close="false"
        size="">
          <div class="dialog-title" slot="title"></div>
          <div class="dialog-content">
            <div class="delete-tip">
              <div class="delete-img">
                <img src="./images/question.png" alt="">
              </div>
              <div class="delete-text">
                
              </div>
            </div>

              <div class="button-group clearfix">
                  <div class="ok-btn left" @click="dialogDelete"></div>
                  <div class="cancel-btn left" @click="dialogDeleteCancel"></div>
              </div>
          </div>
          
        </el-dialog>

        <!--  -->
        <el-dialog class="delete-dialog"
        :visible.sync="muDeleteDialogVisible"
        :show-close="false"
        size="">
          <div class="dialog-title" slot="title"></div>
          <div class="dialog-content">
            <div class="delete-tip">
              <div class="delete-img">
                <img src="./images/question.png" alt="">
              </div>
              <div class="delete-text">
                
              </div>
            </div>

              <div class="button-group clearfix">
                  <div class="ok-btn left" @click="muDialogDelete"></div>
                  <div class="cancel-btn left" @click="dialogDeleteCancel"></div>
              </div>
          </div>
          
        </el-dialog>
    </div>
</template>

<script>
import { getTableList, menuSave } from "../api";
import { ls } from "@/config/pageStore";

export default {
  props: ["draftNum"],
  data() {
    return {
      searchName: "", //
      deleteFoodArr: [], //

      list: null, //
      total: null, //
      listQuery: {
        page: 1,
        limit: 10,
        importance: undefined,
        title: undefined,
        type: undefined,
        sort: "+id"
      },

      //
      isSort: false,
      sortLabel: "",
      sortOrder: 1,

      multipleSelection: [], //
      imgbase_url: imgbase_url, //

      deleteDialogVisible: false, //
      deleteId: "", //id

      muDeleteDialogVisible: false, //

      isListEmpty: true

      // draftNum: 0
    };
  },
  computed: {
    selectCategoryId() {
      return this.$store.state.good.selectCategoryId;
    },
    pageSize() {
      return this.$store.state.good.pageSize;
    }
  },
  created() {
    this.listQuery.limit = ls.getItem("GOODFORMSIZE") || 10;
    this.getList();
    // this.initPageSize();
  },
  watch: {
    selectCategoryId: function() {
      this.getList();
    }
  },
  methods: {
    initTableList() {
      this.list = this.list.map(item => {
        item["food_picture"] = (item.food_img_list || [])[0] || "";

        return item;
      });
    },
    //
    getList() {
      let data = {
        foodlist: 1,
        page_size: this.listQuery.limit,
        page_no: this.listQuery.page,
        category_id: this.selectCategoryId
      };

      if (this.searchName !== "") {
        data["food_name"] = this.searchName;
      }

      //
      if (this.isSort) {
        data["sortby"] = this.sortLabel;
        data["sort"] = this.sortOrder;
      }

      getTableList(data, resp => {
        if (resp.ret === 0) {
          this.list = resp.data.list || [];
          this.total = resp.data.total;
          this.initTableList();

          if (this.list.length !== 0) {
            this.isListEmpty = false;
          } else {
            this.isListEmpty = true;
          }
        } else {
          console.warn("!");
        }
      });
    },

    //
    goAccessEditor() {
      this.$router.push({
        path: "/good/accesseditor",
        query: { isnewAccess: true }
      });
    },

    //
    deleteFoods() {
      let data = {
        del_food: 1,
        food_id_list: this.deleteFoodArr
      };

      menuSave(data, resp => {
        if (resp.ret === 0) {
          this.getList();
          this.$notify({
            title: "",
            message: "",
            type: "success"
          });
        } else {
          console.warn("");
          this.$notify({
            title: "",
            message: "",
            type: "error"
          });
        }
      });
    },

    //
    deleteFood(foodId) {
      this.deleteFoodArr = [];
      this.deleteFoodArr.push(foodId);

      this.deleteFoods();
    },

    //
    multipleDeleteFood() {
      this.deleteFoodArr = [];
      this.deleteFoodArr = this.multipleSelection.map(function(item) {
        return item.food_id;
      });

      if (this.deleteFoodArr.length === 0) {
        this.$notify({
          title: "",
          message: "",
          type: "warning"
        });
        return;
      }
      this.deleteFoods();
    },

    //
    handleSizeChange(val) {
      this.listQuery.limit = val;
      ls.setItem("GOODFORMSIZE", val, 30);
      this.$store.commit("CHANGE_PAGESIZE", val);
      this.getList();
    },
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    },

    //
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    goDraft() {
      this.$router.push({ path: "/good/draft" });
    },
    //pageSize
    initPageSize() {
      this.listQuery.limit = this.pageSize;
    },
    //
    sort(item) {
      this.sortLabel = item.prop;

      if (item.order === "descending") {
        this.isSort = true;
        this.sortOrder = -1;
      } else if (item.order === "ascending") {
        this.isSort = true;
        this.sortOrder = 1;
      } else {
        this.isSort = false;
      }

      this.getList();
    },
    //
    dialogDelete() {
      this.deleteFood(this.deleteId);
      this.deleteDialogVisible = false;
    },
    dialogDeleteCancel() {
      this.deleteDialogVisible = false;
      this.muDeleteDialogVisible = false;
    },
    dialogDeleteOpen(id) {
      this.deleteId = id;
      this.deleteDialogVisible = true;
    },
    muDialogDelete() {
      this.multipleDeleteFood();
      this.muDeleteDialogVisible = false;
    },
    muDialogDeleteOpen() {
      this.muDeleteDialogVisible = true;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#access-table {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

#access-title {
  width: 100%;
}

.table-title {
  @include wh(100%, 40px);
  @include fc(14px, #666666);
  line-height: 40px;
  background-color: #f6f8fc;
  padding-left: 14px;
}

.search-content {
  padding: 0 14px;

  .search-left {
    padding: 20px 0;
    min-width: 500px;
  }

  .search-right {
    padding: 20px 0;
    min-width: 320px;
  }

  .searchBtn {
    margin-left: 20px;
  }
}

.pagination-container {
  text-align: center;
}

.red-text {
  @include fc(14px, #E7487E);
  cursor: pointer;
}

.blue-text {
  @include fc(14px, #4877e7);
  cursor: pointer;

  a {
    @include fc(14px, #4877e7);
  }
}
.table-content {
  padding-left: 14px;
  padding-right: 14px;
  position: relative;

  table {
    img {
      @include wh(60px, 60px);
      vertical-align: middle;
      margin: 10px 0;
    }
  }
}
.delete-dialog {
  .dialog-title {
    @include wh(540px, 4px);
    background-color: #5a8cff;
    line-height: 4px;
  }

  .dialog-content {
    padding: 80px 75px 50px;

    .delete-tip {
      margin-bottom: 60px;

      .delete-img {
        margin-left: 80px;
        display: inline-block;
        @include wh(50px, 50px);
        img {
          @include wh(50px, 50px);
        }
      }

      .delete-text {
        display: inline-block;
        padding-bottom: 15px;
        padding-left: 20px;
        vertical-align: bottom;
        @include fc(14px, #666);
      }
    }
    .button-group {
      margin-top: 40px;

      div {
        @include wh(160px, 40px);
        border-radius: 4px;
        border: 1px solid #5a8cff;
        color: #5a8cff;
        line-height: 40px;
        text-align: center;
        cursor: pointer;
      }
      .ok-btn {
        margin-right: 60px;
        background-color: #5a8cff;
        color: #fff;
      }
    }
  }
}
.data-empty {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  .img {
    @include wh(200px,200px);
    img {
      @include wh(200px, 200px);
    }
  }
  .text {
    @include fc(14px, #9b9b9b);
    width: 200px;
    text-align: center;
    margin-top: 20px;
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#access-table {
  .search-content {
    font-size: 0;

    .el-input {
      font-size: 14px;
      width: inherit;

      input {
        @include wh(180px, 34px);
        border-radius: 0;
      }
    }

    .el-button {
      @include wh(100px, 34px);
      border: 1px solid #4877e7;
      color: #4877e7;
      border-radius: 0;
      font-size: 14px;
    }
    .el-button.red {
      color: #e7487e;
      border-color: #e7487e;
    }
  }
  .el-pagination {
    padding-top: 50px;
    padding-bottom: 14px;
  }
  .el-pagination__jump {
    margin-left: 5px;
    margin-right: 15px;
  }
  .el-table td {
    height: 80px;
  }
  .el-table .cell {
    padding-right: 0;
    padding-left: 0;
  }
  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 0;
  }
}
</style>



